// Media sizes
$desktop-lg: 1920px;
$desktop-md: 1440px;
$desktop: 1200px;
$tablet-landscape: 1024px;
$tablet-portrait: 992px;
$mobile-landscape: 768px;
$mobile-middle: 568px;
$mobile-portrait: 480px;

// Truncate
@mixin text-truncate() {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

// Placeholders
@mixin placeholder {
  &:-moz-placeholder {
    @content;
  }

  &::-moz-placeholder {
    @content;
  }

  &:-ms-input-placeholder {
    @content;
  }

  &::-webkit-input-placeholder {
    @content;
  }
}

// Media query
@mixin max-screen($screen) {
  @media only screen and (max-width: $screen) {
    @content;
  }
}


@mixin min-screen($screen) {
  @media only screen and (min-width: $screen) {
    @content;
  }
}

// Transform center
@mixin posCenter($x, $y) {
  top: $x;
  left: $y;
  transform: translate(-$x, -$y);
}

// max-width & max-height
@mixin max-w-h($size) {
  max-width: $size;
  max-height: $size;
  width: 100%;
  height: 100%;
}