.header {
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 999;
  padding: 16px 0;
  background-color: #000;
  transform: translateY(0);
  transition: transform 0.3s ease;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &.is-animate {
    transform: translateY(-100%);
  }
}

.nav {

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    li {
      margin: 0 14px;

      a {
        font-family: $main-font;
        text-decoration: none;
        font-size: 16px;
        display: inline-block;
        padding: 4px;
        color: $white;
        position: relative;
        overflow: hidden;
        transition: color 0.2s ease-out;


        &:after {
          content: '';
          display: block;
          width: 100%;
          height: 1px;
          background-color: $white;
          position: absolute;
          left: 0;
          bottom: 0;
          transform-origin: left;
          transform: scaleX(0);
          transition: transform 0.2s ease-out;
        }

        &:hover {
          &:after {
            transform: scaleX(1);
          }
        }
      }
    }
  }
}

.btn-burger {
  height: 36px;
  width: 36px;
  background-color: transparent;
  padding: 0;
  display: none;
  position: relative;

  @media only screen and (max-width: 1024px) {
    display: block;
  }

  span {
    position: absolute;
    display: block;
    width: 100%;
    height: 2px;
    background-color: $white;
    transition: 0.3s ease;
  }

  &.is-open {}
}