.overlay {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000;
  // background: rgba(8, 7, 21, 0.5);
  opacity: 0;
  visibility: hidden;
  z-index: -1;
  position: fixed;
  width: 100%;
  height: 100%;
  transition: opacity 0.2s linear, visibility 0.2s linear;

  &.is-visible {
    opacity: 0.8;
    visibility: visible;
    z-index: 100;
  }
}

.main-page {
  background-image: url(../img/bg-space.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.main-holder {
  height: 100%;
  width: 100%;
  position: relative;
  // display: none;
  // animation-name: orbit-reverse;
  // animation-iteration-count: infinite;
  // animation-timing-function: linear;
  // animation-duration: 220s;
}

.main-block {
  position: absolute;
  z-index: 100;
  @include max-w-h(280px);
  @include posCenter(50%, 50%);

  @media only screen and (max-width: 1830px) and (max-height: 860px) {
    @include max-w-h(240px);
  }

  @media only screen and (max-width: 1780px) and (max-height: 860px) {
    @include max-w-h(160px);
  }

  @media only screen and (max-width: 1530px) and (max-height: 720px) {
    @include max-w-h(120px);
  }

  @media only screen and (max-width: 1024px) {
    @include max-w-h(110px);
  }

  &__link {
    display: block;
    position: relative;
    width: 100%;
    height: 100%;
    transform: scale(1);
    transition: transform 0.2s linear;

    &:hover {
      transform: scale(0.9);
    }
  }

  &__decor {
    position: absolute;
    z-index: 1;
    animation-name: orbit-reverse;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-duration: 40s;
    transform: rotate(0);
  }

  &__logo {
    position: absolute;
    z-index: 10;
    @include posCenter(50%, 50%);
    @include max-w-h(140px);

    @media only screen and (max-width: 1830px) and (max-height: 860px) {
      @include max-w-h(100px);
    }

    @media only screen and (max-width: 1780px) and (max-height: 860px) {
      @include max-w-h(80px);
    }

    @media only screen and (max-width: 1992px) and (max-height: 860px) {
      @include max-w-h(60px);
    }

    @media only screen and (max-width: 1530px) and (max-height: 720px) {
      @include max-w-h(40px);
    }
  }
}

.circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotateZ(0);
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  list-style: none;
  animation-name: orbit;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-duration: 140s;

  @media only screen and (max-width: 992px) {
    animation-name: none;
    border-radius: 0;
    border: none;
    position: unset;
    transform: none;
  }

  &--xl {
    @include max-w-h(890px);

    @media only screen and (max-width: 1830px) and (max-height: 860px) {
      @include max-w-h(790px);
    }

    @media only screen and (max-width: 1780px) and (max-height: 860px) {
      @include max-w-h(690px);
    }

    @media only screen and (max-width: 1530px) and (max-height: 720px) {
      @include max-w-h(640px);
    }

    @media only screen and (max-width: 1024px) {
      @include max-w-h(600px);
    }

    @media only screen and (max-width: 992px) {
      max-width: 100%;
      max-height: none;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    >.circle__items {
      &:first-child {
        top: 78px;
        left: 100px;

        @media only screen and (max-width: 1780px) {
          top: 46px;
          left: 96px;
        }

        @media only screen and (max-width: 1024px) {
          top: 22px;
        }
      }

      &:nth-child(2) {
        top: 200px;
        right: 0;

        @media only screen and (max-width: 1780px) {
          top: 180px;
        }

        @media only screen and (max-width: 1024px) {
          right: -18px;

        }
      }

      &:nth-child(3) {
        bottom: 14px;
        right: 180px;

        @media only screen and (max-width: 1780px) {
          right: 150px;
        }

        @media only screen and (max-width: 1024px) {
          bottom: -8px;
        }
      }

      &:nth-child(4) {
        bottom: 14px;
        left: 180px;

        @media only screen and (max-width: 1780px) {
          bottom: -8px;
        }

        @media only screen and (max-width: 1024px) {
          bottom: -20px;
        }
      }

      &:nth-child(5) {
        bottom: 250px;
        left: -30px;
      }
    }
  }

  &--l {
    @include max-w-h(762px);

    @media only screen and (max-width: 1830px) and (max-height: 860px) {
      @include max-w-h(662px);
    }

    @media only screen and (max-width: 1780px) and (max-height: 860px) {
      @include max-w-h(562px);
    }

    @media only screen and (max-width: 1530px) and (max-height: 720px) {
      @include max-w-h(530px);
    }

    @media only screen and (max-width: 1024px) {
      @include max-w-h(500px);
    }

    @media only screen and (max-width: 992px) {
      max-width: 100%;
      max-height: none;
      display: flex;
    }

    >.circle__items {
      &:first-child {
        top: 0;
        left: 180px;

        @media only screen and (max-width: 1780px) {
          top: -20px;
        }
      }

      &:nth-child(2) {
        top: 0;
        left: 502px;

        @media only screen and (max-width: 1780px) {
          left: 376px;
        }

        @media only screen and (max-width: 1024px) {
          top: 25px;
        }

        // @media only screen and (max-width: 1024px) {
        //   left: 430px;
        // }
      }

      &:nth-child(3) {
        top: 348px;
        right: -34px;

        @media only screen and (max-width: 1780px) {
          right: -10px;
        }

        @media only screen and (max-width: 1024px) {
          right: 5px;
        }
      }

      &:nth-child(4) {
        top: 400px;
        left: -34px;

        @media only screen and (max-width: 1780px) {
          top: 278px;
        }

        @media only screen and (max-width: 1024px) {
          left: -26px;
        }
      }
    }
  }

  &--m {
    @include max-w-h(634px);

    @media only screen and (max-width: 1830px) and (max-height: 860px) {
      @include max-w-h(534px);
    }

    @media only screen and (max-width: 1780px) and (max-height: 860px) {
      @include max-w-h(434px);
    }

    @media only screen and (max-width: 1530px) and (max-height: 720px) {
      @include max-w-h(400px);
    }

    @media only screen and (max-width: 1024px) {
      @include max-w-h(370px);
    }

    @media only screen and (max-width: 992px) {
      max-width: 100%;
      max-height: none;
      display: flex;
      // position: absolute;
      // top: 120px;
    }

    >.circle__items {
      &:first-child {
        top: 460px;
        right: 22px;

        @media only screen and (max-width: 1780px) {
          top: 330px;
        }

        @media only screen and (max-width: 1024px) {
          top: 278px;
        }
      }
    }
  }

  &--s {
    @include max-w-h(506px);

    @media only screen and (max-width: 1830px) and (max-height: 860px) {
      @include max-w-h(406px);
    }

    @media only screen and (max-width: 1780px) and (max-height: 860px) {
      @include max-w-h(306px);
    }

    @media only screen and (max-width: 1530px) and (max-height: 720px) {
      @include max-w-h(280px);
    }

    @media only screen and (max-width: 1024px) {
      @include max-w-h(250px);
    }

    @media only screen and (max-width: 992px) {
      max-width: 100%;
      max-height: none;
      display: flex;
    }

    >.circle__items {
      &:first-child {
        top: 0;
        left: 80px;

        @media only screen and (max-width: 1780px) {
          left: 36px;
        }

        @media only screen and (max-width: 1024px) {
          top: -12px;
        }
      }

      &:nth-child(2) {
        top: 100px;
        right: -6px;

        @media only screen and (max-width: 1780px) {
          right: -24px;
        }
      }

      &:nth-child(3) {
        bottom: 0;
        left: 80px;

        @media only screen and (max-width: 1780px) {
          bottom: -28px;
        }
      }
    }
  }

  &--xs {
    @include max-w-h(378px);

    @media only screen and (max-width: 1830px) and (max-height: 860px) {
      @include max-w-h(278px);
    }

    @media only screen and (max-width: 1780px) and (max-height: 860px) {
      @include max-w-h(178px);
    }

    @media only screen and (max-width: 1530px) and (max-height: 720px) {
      @include max-w-h(140px);
    }

    @media only screen and (max-width: 1024px) {
      @include max-w-h(110px);
    }

    @media only screen and (max-width: 992px) {
      max-width: 100%;
      max-height: none;
    }

    .circle__items {
      animation-name: orbit-reverse;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
      animation-duration: 140s;

      @media only screen and (max-width: 992px) {
        animation-name: none;
      }

      .circle__img {
        img {}
      }

      &:first-child {
        top: 160px;
        left: -36px;

        @media only screen and (max-width: 1780px) {
          top: 50px;
        }
      }

      &--main {
        width: 87px;
        height: 87px;

        @media only screen and (max-width: 1780px) {
          width: 60px;
          height: 60px;
        }
      }
    }
  }

  &__items {
    position: absolute;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: $white;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.8);
    cursor: pointer;

    @media only screen and (max-width: 992px) {
      position: unset;
      width: 80px;
      height: 80px;
      margin: 0 15px;
    }

    &:hover {
      .circle__img {
        img {
          transform: scale(1.1);
        }
      }
    }

    &--main {
      width: 66px;
      height: 66px;
    }

    &--bg {
      background: linear-gradient(162.46deg, #6A7FFE 0%, #5B8CFD 37.5%, #4E9FFD 76.56%, #49A4FC 98.44%);
    }
  }

  &__info {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    color: transparent;
    width: 1px;
    height: 1px;
    overflow: hidden;
  }

  &__link {}

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    img {
      transform-origin: center;
      transform: scale(1);
      transition: transform 0.2s linear;
    }
  }

  .js-marquee {
    @media only screen and (max-width: 992px) {
      display: flex;
    }
  }
}

// Descr modal
.descr-modal {
  position: absolute;
  @include posCenter(50%, 50%);
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  max-width: 420px;
  min-height: 370px;
  width: 100%;
  height: auto;
  background: rgba(0, 0, 0, 0.7);
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  padding: 24px;
  transition: opacity 0.2s linear,
    visibility 0.2s linear;

  &__close {
    position: absolute;
    top: 1px;
    right: 1px;
    width: 36px;
    height: 36px;
    border-width: 1px;
    border-style: solid;
    border-color: transparent;
    border-radius: 10px;
    background-color: transparent;
    opacity: 0.8;
    transform: rotate(0);
    transform-origin: center;
    transition: transform 0.2s ease, border-color 0.3s ease-in-out;

    &:after,
    &:before {
      content: '';
      position: absolute;
      background-color: $white;
      border-radius: 2px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &:after {
      width: 30px;
      height: 3px;
    }

    &:before {
      width: 3px;
      height: 30px;
    }

    &:hover {
      opacity: 1;
      transform: rotate(360deg);
      border-color: $white;
    }
  }

  &__holder {
    margin-bottom: 40px;
  }

  &__title {
    font-size: 20px;
    line-height: 24px;
    font-weight: 700;
    color: $white;
    margin: 0 20px 16px;
  }

  &__wrapper {
    font-size: 14px;
    line-height: 20px;
    font-weight: 500;
    color: $white;
    margin-bottom: 16px;

    p {
      &:last-child {
        margin: 0;
      }
    }
  }

  &__caption {
    display: inline-block;
    margin-bottom: 8px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 600;
    color: #9B84A3;
    text-align: left;
  }

  &__icon {
    display: flex;
    align-items: center;

    font-style: normal;
    text-align: left;

    border: 1px solid $white;
    border-radius: 10px;
  }

  &__link {
    padding: 8px 32px;
    display: block;
    width: 100%;
    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    color: $white;
    text-decoration: none;
    position: relative;
    opacity: 1;
    transition: opacity 0.2s ease-in-out;

    &:hover {
      opacity: 0.7;
      text-decoration: underline;

      &:after {
        transform: translate(10px, -50%);
      }
    }

    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 32px;
      transform: translate(0, -50%);
      width: 21px;
      height: 16px;
      background: url(../../img/icon-arrow.svg) no-repeat center;
      background-size: contain;
      transition: transform 0.3s linear;
    }
  }

  &__logo {
    position: absolute;
    left: 50%;
    bottom: -44px;
    transform: translate(-50%, 0);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 88px;
    height: 88px;
    background-color: $white;
    border-radius: 50%;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.8);
    overflow: hidden;
  }

  &.is-open {
    opacity: 1;
    visibility: visible;
    z-index: 999;
  }
}

/* Animations */
@keyframes orbit {
  from {
    transform: translate(-50%, -50%) rotateZ(0);
  }

  to {
    transform: translate(-50%, -50%) rotateZ(-360deg);
  }
}

@keyframes orbit-reverse {
  from {
    transform: rotateZ(-360deg);
  }

  to {
    transform: rotateZ(0);
  }
}















@keyframes orbit-test {
  from {
    transform: rotateZ(-360deg);
  }

  to {
    transform: rotateZ(0);
  }
}

@keyframes orbit-img {
  from {
    transform: rotateZ(0);
  }

  to {
    transform: rotateZ(360deg);
  }
}






